.client{
    .container{
        padding: auto;
    }

    .container-options{
        padding-top: 10rem;
        @include grid(3,5rem);
    }

    .option-client{
        text-align: center;
        border: 3px solid $color5;
        border-radius: 2rem;
        h1{
            margin: 1rem 0;
        }
        img{
            border-radius: 2rem;
            width: 90%;
            margin-bottom: 1rem;
        }
    }

    .option-client:hover{
        cursor: pointer;
        background-color: $color3;
    }

    .option-dual{
        @include grid(2,0.2rem);
        img{
            border-radius: 2rem;
            width: 98%;
            padding: 0 0.5rem;
        }
    }

    .cliente{
        padding-top: 7rem;
    }
    .nav-top{
        position: fixed;
        width: 100%;
        background-color: $color5;
        display: grid;
        grid-template-columns: repeat(3,1fr);
        margin: auto;
        p{
            color: $color1;
        }
        .animacion-menu{
            padding: 1rem 0;
        }
    }

    .acciones-client{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        form{   
            margin: auto;
        }
    }
    .servicios,
    .show-inventario{
        @include grid(4,3rem);
    }
    .servicio,
    .producto{
        background-color: $color3;
        text-align: center;
        border-radius: 2rem;
        span{
            color: $color5;
        }
        .titulo{
            font-weight: 600;
            font-size: 2.4rem;
        }
    }
    .servicio:hover,
    .producto:hover{
        cursor: pointer;
        background-color: darken($color3, 15);
    }
    .servicio-seleccionado,
    .producto-seleccionado{
        background-color: $color4;
        p{
            color: $color1;
        }
        span{
            font-weight: 600;
            color: $color2;
        }
    }
    .servicio-seleccionado:hover,
    .producto-seleccionado:hover{
        background-color: darken($color4, 8);
    }
    .titulo-nav{
        padding-top: 7rem;
    }
    .btn-paginador-sig{
        text-align: end;
        padding: 2rem;
        button{
            font-size: 1.8rem;
            padding: 1.5rem 2rem;
        }
    }
    .btn-paginador-dos{
        display: flex;
        justify-content: space-between;
        padding: 2rem;
        button{
            font-size: 1.8rem;
            padding: 1.5rem 2rem;
        } 
    }
    .paginador{
        display: flex;
        justify-content: space-between;
        padding-top: 2rem;
        button{
            padding: 1.5rem 2rem;
            font-size: 1.8rem;
        }
    }
    .seccion{
        display: none;
    }
    .contenedor-servicio,
    .contenedor-producto{
        display: flex;
        justify-content: space-between;
    }
    .contenedor-precio{
        text-align: end;
    }
    .total{
        span{
            color: $color5;
        }
    }
    .btn-reservar{
        margin-top: 2rem;
        padding: 1rem 2rem;
    }
    .list-product{
        display: flex;
        p{
            margin: 0;
        }
        input{
            width: 6rem;
            height: 2rem;
        }
    }
}
.mostrar-seccion {
    display: block!important;
}